@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700;900&family=Raleway:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  color: white;
}

#header {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;

  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  transition: all 1s;
}
@media only screen and (max-width: 676px) {
  .nav-menu ul {
    display: flex;
    /* margin: 0; */
    padding: 0;
    justify-content: space-evenly;
    /* background: red; */
  }
  #header {
    top: 2%;
    right: 0;
    bottom: auto;
    display: flex;
    flex-direction: row;
    /* background: blue; */
    flex-direction: column;
    margin: 0;
  }
}

.nav-menu * {
  text-decoration: none;
}

.nav-menu ul {
  list-style: none;
}

.nav-menu > ul > li {
  display: flex;

  align-items: center;
  position: relative;
  white-space: nowrap;

  height: 30px;
  margin: 20px 0;
  padding: 28px 0;
}
.nav-menu > ul > li > a {
  font-weight: bold;
  color: white;
  width: 55px;
  background-color: rgba(0, 0, 0, 0.233);
  border-radius: 999px;
  display: flex;
  align-items: center;
  transition: 0.5s;
}
.nav-menu > ul > li > a > i {
  font-size: 20px;
  padding: 18px 8px 18px 18px;
}

.nav-menu > ul > li > a > span {
  display: none;
  padding-right: 15px;
}
.nav-menu > ul > li > a:hover span {
  display: block;
}
.nav-menu > ul > li > a:hover {
  background-color: white;
  color: black;
  width: 100%;
}

li {
  list-style: none;
}

.hero {
  height: 100vh;
  width: 100%;
  background-color: #000000;
  color: #fff;
  position: relative;
  background: #0000008a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 15%;
}

.hero:before {
  content: "";
  position: fixed;
  background: #040404 url(/swaraj344.github.io/static/media/man2.c5ccf11d.jpg) top right no-repeat;
  background-size: cover;
  left: 0;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: -1;
}
.hero h1 {
  /* background-color: red; */
  margin: 0;
  font-size: 4.5rem;
  font-weight: 700;
  line-height: 56px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
@media only screen and (max-width: 876px) {
  .hero h1 {
    font-size: 3.5rem;
  }
  .hero {
    padding-left: 0%;
    align-items: center;
  }
}

.hero p {
  /* background-color: yellow; */
  margin-top: 14px;
  font-size: 24px;
  font-family: "Poppins", sans-serif;
  transition: 0.3;
}
.hero ul {
  /* background-color: violet; */
  display: flex;
  margin-top: 24px;
  padding-left: 0 !important;
}
.hero ul li a i {
  text-decoration: none;
  color: white;
  font-size: 24px;
  margin-right: 18px;
  transition: 0.3s;
}
.hero ul li a i:hover {
  color: blue;
  transform: translateY(-2px);
}
.typed {
  color: #0880f0;
  /* font-size: 24px; */
  font-family: "poppins" sans-serif;
  /* background: transparent; */
}

.darkBg {
  background: #2b2b2b;
}

.about-left-section {
  padding: 20px 0;
}
.about-left-section p {
  color: #ababab;
}

.edu-detail-card {
  height: 300px;
  margin: 20px 0;
  padding: 0 20px;
}
@media only screen and (max-width: 876px) {
  .edu-detail-card {
    height: auto;
    /* align-items: center;
    justify-content: center; */
  }
}
.edu-subtitle {
  background-color: #5e60ce;
  border-radius: 10px;
  margin: 20px 0;
  padding: 8px 20px;
}

#resume .title {
  position: relative;
  color: #00509d;

  padding: 60px 0 20px 0;
}
#resume .title::after {
  position: absolute;
  content: "";
  height: 2px;
  bottom: 10px;

  display: flex;
  width: 100%;
  background: #00519dad;
}
/* progress bar code */
.progress-wrapper {
  margin: 20px auto;
  padding: 10px;
  max-width: 110px;
  max-height: 110px;
}
.skill__card {
  margin: 10px 0px;
  min-height: 345px;
  padding: 10px 5px;
  align-items: center;
  transition: 0.5s;
}
.skill__card:hover {
  transform: translateY(-20px);
}
