}
#contact .contact__wrapper {
  padding: 10% 50px;
}

#footer {
  background-color: #2b2b2b;
  text-align: center;
  padding: 30px;
}
#footer h3 {
  padding-top: 40px;
  font-weight: bold;
  font-family: poppins, "Times New Roman", Times, serif;
  font-size: 2.5rem;
}

@media only screen and (max-width: 876px) {
